<template>
  <footer>
    <section class="footer_top">
      <div class="footer_top_item about">
        <h3 class="item_title">关于</h3>
        <div class="item_content">
          <p class="about_text">热爱 coding、编程和敲代码，平时喜欢打球、跑步，为人真实不做作</p>
          <ul class="list">
            <li v-for="info in contactInfoList" class="list_item">
              <i class="iconfont" :class="info.iconfont" style="font-size: 25px"></i>
              <span>{{info.info}}</span>
            </li>
          </ul>
        </div>
      </div>

      <div class="footer_top_item footer_image">
        <img src="https://gitee.com/sculpta/data/raw/master/shenke/shenke.jpg">
      </div>

      <div class="footer_top_item">
        <h3 class="item_title">项目经历</h3>
        <div class="item_content">
          <ul class="list">
            <li v-for="item in projectList" class="list_item">
              <a :href="item.link">{{item.name}}</a>
            </li>
          </ul>
        </div>
      </div>

      <div class="footer_top_item">
        <h3 class="item_title">奖项荣誉</h3>
        <div class="item_content">
          <ul class="list">
            <li v-for="award in awardList" class="list_item">
              <p>{{award}}</p>
            </li>
          </ul>
        </div>
      </div>
    </section>

    <section class="footer_bottom">
      <p class="copyright">© All rights reserved {{copyrightYear}} <a href="https://tsund.cn">深刻</a> <a
        href="http://www.beian.miit.gov.cn">鲁ICP备20005753号-1</a></p>

      <ul class="social">
        <li v-for="social in socialList" class="social_item">
          <a :href="social.link">
            <i class="iconfont" :class="social.iconfont"></i>
            <p>{{social.name}}</p>
          </a>
        </li>
      </ul>
    </section>


  </footer>
</template>

<script>
  // 引入 iconfont
  import "../assets/iconfont/iconfont.js"
  import "../assets/iconfont/iconfont.css"

  export default {
    name: "Footer",
    data() {
      return {
        contactInfoList: [
          {'iconfont': 'icon-weizhi', 'info': 'Xidian University, Shaanxi, China'},
          {'iconfont': 'icon-youxiang', 'info': 'shenkebug@gmail.com'},
        ],
        projectList: [
          {'name': '博客', 'link': 'https://blog.tsund.cn'},
          {'name': 'CoolTran', 'link': 'https://github.com/sculpta/CoolTran'}
        ],
        awardList: ['2019年蓝桥杯国三'],
        socialList: [
          {
            'name': 'GitHub',
            'iconfont': 'icon-GitHub',
            'link': 'https://github.com/sculpta'
          },
          {
            'name': 'Twitter',
            'iconfont': 'icon-twitter',
            'link': 'https://twitter.com/tsundcn'
          },
          {
            'name': 'Ins',
            'iconfont': 'icon-Instagram',
            'link': 'https://instagram.com/tsundcn'
          },
          {
            'name': 'Telegram',
            'iconfont': 'icon-telegram',
            'link': 'https://t.me/tsund'
          },
        ],
        copyrightYear:
            1900
      }
    },
    created() {
      let year = new Date().getFullYear();
      this.copyrightYear = year;
    }
  }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  footer, section {
    display: block;
  }

  /*
  footer_top
   */
  .footer_top {
    background-color: #1a1818;
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    color: #fff;

    -ms-flex-align: start;
    align-items: flex-start;

    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;

    -ms-flex: 1;
    flex: 1;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
  }

  /*
  关于
  */
  .footer_top_item, .about {
    -ms-flex: 1;
    flex: 1;
    width: 100%;
    padding: 0 15px 15px;
  }

  .about {
    -ms-flex: 2;
    flex: 2;
  }

  .item_title {
    -ms-flex: 2;
    flex: 2;
    color: #fff;
    font-size: 16px;
    margin: 30px 0;
    text-transform: capitalize;
  }

  .item_content {
    color: #999;
    font-size: 13px;
  }

  .about_text {
    font-size: 14px;
  }

  .list {
    list-style: none;
    line-height: 1;
    font-size: 13px;
  }

  .list_item {
    margin-top: 20px;
    color: #69747a;
    font-size: 14px;
  }

  .list_item span {
    vertical-align: middle;
  }

  .list a {
    color: #69747a;
  }

  /*
  图片
  */
  .footer_image {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-item-align: stretch;
    align-self: stretch;
  }

  .footer_image img {
    display: block;
    width: 100%;
    height: auto;
    max-width: 150px;
    margin-top: 30px;
  }

  /*
  footer_bottom
   */
  .footer_bottom {
    width: 100%;
    margin: 0 auto;
    padding: 20px 150px;
    display: block;
    background-color: #1a1818;
    color: #fff;

    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;

    flex: 1;
    position: relative;
  }

  .copyright {
    margin: 0;
    font-size: 13px;
    text-align: center;
  }


  .copyright a {
    color: white;
  }

  .copyright a:hover {
    color: goldenrod;
  }

  .social {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 24px;
    line-height: 1.2;
  }

  .social, .social_item {
    float: left;
    transition: transform .3s;
    margin: 0 10px;
    text-align: center;
  }

  .social a {
    font-size: inherit;
    text-decoration: none;
    transition: color .3s;
    color: #fff;
  }

  .social p {
    font-size: 12px;
  }

  @media (max-width: 1024px) {
    .footer_top {
      -ms-flex-direction: column;
      flex-direction: column;
      float: left;
    }

    .footer_bottom {
      -ms-flex-direction: column;
      flex-direction: column;
      padding: 20px 0;
    }

    .social {
      margin-top: 20px;
    }

  }

</style>



