<template>
  <div id="content">

    <div class="box" id="introduce" style="background-color: #F5F5F5">
      <transition name="fade">
        <div id="message" v-if="isShowList[0].isShow">
          <p>Hi! I'm Ji Yuanhao「吉元昊」,</p>
          <p>I graduated from <strong>Liaocheng University</strong> majoring in software engineering, now I am a
            graduate student of <strong>Xidian University.</strong></p>
        </div>
      </transition>
    </div>

    <div class="box" style="background-color: white">
      <transition name="fade">
        <h3 v-if="isShowList[1].isShow">Loading . . .</h3>
      </transition>
    </div>

    <div class="box" style="background-color: #F5F5F5">
      <transition name="fade">
        <h3 v-if="isShowList[2].isShow">Loading . . .</h3>
      </transition>
    </div>

    <div class="box" style="background-color: white">
      <transition name="fade">
        <h3 v-if="isShowList[3].isShow">Loading . . .</h3>
      </transition>
    </div>

  </div>
</template>

<script>
  export default {
    name: "Content",
    data() {
      return {
        isShowList: [
          {isShow: false},
          {isShow: false},
          {isShow: false},
          {isShow: false},
        ],
        progress: 0
      }
    },
    mounted() {
      //  useCapture——true: 外部元素的事件先触发
      window.addEventListener('scroll', this.handleScroll, true);

      this.$Loading.update(this.progress);
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods: {
      //  处理滚动事件
      handleScroll: function () {
        //  动态显示每一个盒子
        const boxes = document.querySelectorAll('.box');
        let length = boxes.length;

        const viewPortHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop <= viewPortHeight / 4) {
          this.progress = 0;
          this.$Loading.update(this.progress);
        } else {
          for (let index = 0; index < length; index++) {
            let element = boxes[index];
            let top = element.getBoundingClientRect() && element.getBoundingClientRect().top;

            let rule = element.clientHeight * 0.75;
            if (top <= viewPortHeight - rule) {
              if (!this.isShowList[index].isShow) {
                this.isShowList[index].isShow = true;
              }
              this.progress = (index + 1) / length * 100;
              this.$Loading.update(this.progress);
            }
          }
        }
      },
    },
    watch: {},
  }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    text-align: center;
  }

  .box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  #message p {
    color: black;
    font-family: "Nunito", Arial, sans-serif;
    line-height: 2;
  }

  h3 {
    color: black;
    font-family: "Nunito", Arial, sans-serif;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
    transform: translateY(100px);
  }

  .fade-enter-to, .fade-leave {
    opacity: 1;
  }

  .fade-enter-active, .fade-leave-active {
    transition: all 1s;
  }

  @media (max-width: 1024px) {
    /* width < 1024 */
    .box {
      height: 300px;
    }

    #message {
      max-width: 90%;
    }

    #message p, h3 {
      font-size: 1.25rem;
    }
  }

  @media (min-width: 1024px) {
    /* width >= 1024 */
    .box {
      height: 500px;
    }

    #message {
      max-width: 70%;
    }

    #message p, h3 {
      font-size: 2rem;
    }
  }

</style>



