<template>
  <div id="page">

    <div v-if="showLoading">
      <div class="loader" @touchmove.prevent @mousewheel.prevent>
        <Spin fix>
          <Icon type="ios-loading" class="loading-icon" size="50"></Icon>
          <div style="font-size:20px;color:forestgreen;">Loading</div>
        </Spin>
      </div>
    </div>

    <header ref="header" v-if="!showLoading">
      <transition name="fade">
        <h2 id="title" v-if="showTitle">You're unique,<br>nothing can replace you.</h2>
      </transition>
    </header>

    <Content></Content>

    <Footer></Footer>
  </div>
</template>

<script>
  import Content from "./components/Content";
  import Footer from "./components/Footer";

  export default {
    name: "Page",
    data() {
      return {
        showLoading: false,
        showTitle: false,
        clientHeight: '',
      }
    },
    components: {
      Content, Footer
    },

    beforeCreate() {
      this.$Spin.show();
    },

    created() {
      clearTimeout(this.timer);  //清除延迟执行

      this.timer = setTimeout(() => {   //设置延迟执行
        this.$Spin.hide();
        this.showLoading = false;

        // 获取浏览器可视区域高度
        const _this = this;
        this.clientHeight = document.documentElement.clientHeight;
        window.onresize = function temp() {
          _this.clientHeight = document.documentElement.clientHeight;
        };

        // 让header中的元素动态显示
        this.showTitle = true;
      }, 1000);

    },

    mounted() {
      // // 获取浏览器可视区域高度
      // const _this = this;
      // this.clientHeight = document.documentElement.clientHeight;
      // window.onresize = function temp() {
      //   _this.clientHeight = document.documentElement.clientHeight;
      // };
      //
      // // 让header中的元素动态显示
      // this.showTitle = true;
    },

    methods: {
      // 动态修改样式
      changeFixed(clientHeight) {
        this.$refs.header.style.height = clientHeight + 'px';
      },
    },

    watch: {
      // 如果 `clientHeight` 发生改变，这个函数就会运行
      clientHeight: function () {
        this.changeFixed(this.clientHeight);
      }
    }
  }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  .loader {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 99;
  }

  .loading-icon {
    color: forestgreen;
    animation: loading-ani 1s linear infinite;
  }

  @keyframes loading-ani {
    from {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(180deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  header {
    width: 100%;
    text-align: center;

    background-image: url("https://gitee.com/sculpta/data/raw/master/homepage/wallpaper.jpg");
    background-repeat: no-repeat;
    position: relative;
    top: 0;
    left: 0;
    zoom: 1;
    background-size: cover;
    -webkit-background-size: cover;
    -o-background-size: cover;
    background-position: center 0;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  #title {
    color: white;
    font-family: "Nunito", Arial, sans-serif;
    font-weight: 400;
    font-size: 4rem;
    max-width: 80%;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
    transform: translateY(100px);
  }

  .fade-enter-to, .fade-leave {
    opacity: 1;
  }

  .fade-enter-active, .fade-leave-active {
    transition: all 1s;
  }

  @media (max-width: 1024px) {
    /* width < 1024 */
    #title {
      font-size: 2.75rem;
    }
  }

  @media (min-width: 1024px) {
    /* width >= 1024 */
    #title {
      font-size: 4.5rem;
    }
  }

</style>



